*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "PT Sans bold";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
.button {
  background-color: #81B3D2;
  border: none;
  text-align: center;
  text-decoration: none;
  font-family: "PT Sans bold", "Arial", sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 21px;
  line-height: 26px;
  color: #ffffff;
  padding: 16px 0 16px;
}
.button:hover,
.button:focus {
  background-color: #669EC0;
  cursor: pointer;
}
.button:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.6);
}
.button:disabled {
  background-color: #E5E5E5;
}
.input {
  border: 4px solid transparent;
  min-width: 150px;
  min-height: 58px;
  padding: 10px 22px 6px 20px;
  font-family: "PT Sans bold", "Arial", sans-serif;
  font-weight: normal;
  text-transform: none;
  font-size: 18px;
  line-height: 26px;
  background-color: #f2f2f2;
  outline: none;
}
.input:hover {
  background-color: #E5E5E5;
  cursor: pointer;
}
.input:focus {
  padding: 10px 22px 6px 20px;
  border: 4px solid #E5E5E5;
  background-color: #ffffff;
}
.input--warning {
  border: 4px solid red;
}
